import React, { useState } from "react";
import thumbnailimg from '../../assets/images/video-thumbnail-img.png'
import playbtn from '../../assets/images/play-btn-icon.png'
import tringleicon from '../../assets/images/traingle-icon.png'
import { Modal, ModalBody
} from "reactstrap"

function Lore() {
  // Modal open state
  const [modal, setModal] = useState(false);

  // Toggle for Modal
  const toggle = () => setModal(!modal);
  return (
    <div className="lore-sec">
      <div className="container">
        <div className="lore-desc">
          <h3>
            Once upon a time, in the far reaches of the universe,
            <br />
            there was a <span>unique galaxy</span> with a{" "}
            <span>curious evolution</span>.
          </h3>
          <h1 className="placeholder-text">LORE</h1>
        </div>
        <div className="lore-video-sec">
          <img src={thumbnailimg} />
          <a href="javascript:void(0)" onClick={toggle}>
            <img src={playbtn} className="playbtn" />
          </a>
        </div>
      </div>
      <Modal isOpen={modal} toggle={toggle}>
        <ModalBody>
          <div className="video-popup">
            <iframe
              width="100%"
              height="345"
              src="https://www.youtube.com/embed/tgbNymZ7vqY"
            ></iframe>
            <button className="close-btn" onClick={toggle}>
              X
            </button>
          </div>
        </ModalBody>
      </Modal>
      <img src={tringleicon} className="tringleicon" />
    </div>
  );
}

export default Lore;
