import React, { useEffect, useRef, useState } from "react";
import Scene from "../../components/Models/Scene";
import Banner from "../../components/Bannaer/Banner";
import CTA from "../../components/CTA/CTA";
import Features from "../../components/Features/Features";
import Gravitars from "../../components/Gravitars/Gravitars";
import Introduction from "../../components/Introduction/Introduction";
import Lore from "../../components/Lore/Lore";
import Roadmap from "../../components/Roadmap/Roadmap";
import Header from "../../components/Header/Header";
import Footer from "../../components/Footer/Footer";
import "bootstrap/dist/css/bootstrap.css";
import "../../assets/scss/common.scss";
import LoaderVideo from "../../components/LoaderVideo/LoaderVideo";
import patternbg from "../../assets/images/pattern-bg.png";

function Home() {
  const activeRef = useRef({
    banner: true,
    intro: false,
    lore: false,
    features: false,
    gravitars: false,
    cta: false,
  });

  const scroll = useRef(0);
  const divRef = useRef(null);
  const handleScroll = (e) => {
    scroll.current =
      window.scrollY / (divRef.current.scrollHeight - window.innerHeight);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <>
      <div ref={divRef} className="canvas-sec" style={{ overflowX: "hidden" }}>
        {/* <LoaderVideo src="./videos/Gravitale_Placeholder_LogoAnimation.mp4" /> */}
        <Header />
        <Banner activeRef={activeRef} />
        <Introduction activeRef={activeRef} />
        <Lore activeRef={activeRef} />
        <Features activeRef={activeRef} />
        <Gravitars activeRef={activeRef} />
        <Roadmap />
        <CTA activeRef={activeRef} />
        <Footer />
        <Scene scroll={scroll} />
        <div
          className="patternbg"
          style={{
            backgroundImage:
              "url(' + patternbg + ')no-repeat center center/cover",
          }}
        ></div>
        {/* <img src={patternbg} className='patternbg' /> */}
        <ul className="sidebar-icon-list">
          <li></li>
          <li className="active"></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </div>
    </>
  );
}

export default Home;
