import React,{ useState,useEffect } from "react";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  NavbarText,
} from 'reactstrap';
import logo from '../../assets/images/logo.png'

function Header(args) {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);
  const [scroll, setScroll] = useState(false);
 useEffect(() => {
   window.addEventListener("scroll", () => {
     setScroll(window.scrollY > 50);
   });
 }, []);
  return(
  <div>
    <Navbar {...args} expand="md" className={scroll ? 'header sticky' : 'header'}>
        <NavbarBrand href="/"><img src={logo} /></NavbarBrand>
        <NavbarToggler onClick={toggle} />
        <div className="container">
        <Collapse isOpen={isOpen} navbar>
          <Nav className="me-auto" navbar>
            <NavItem>
              <NavLink href="/components/">Hero</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="https://github.com/reactstrap/reactstrap">
              INTRODUCTION
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="https://github.com/reactstrap/reactstrap">
              LORE
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="https://github.com/reactstrap/reactstrap">
              FEATURES
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="https://github.com/reactstrap/reactstrap">
              GRAVITARS
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="https://github.com/reactstrap/reactstrap">
              ROADMAP
              </NavLink>
            </NavItem>
          </Nav>
        </Collapse>

        </div>
        <NavLink href="https://github.com/reactstrap/reactstrap" className="common-btn">
          Join Us
        </NavLink>
      </Navbar>
  </div>);
}

export default Header;
